import { Position, RecordType, Round, Wind } from '../types'

export type PostGameRecordRequestType = {
  ownerPositionName: string
}

export type PostAddNewUserRequestType = {
  position: string
  gameRecordId: string
}

export type StartGameRequestType = {
  gameRecordId: string
  base: number
  bonus: number
  startDealerPosition: string
}

export type EndGameRequestType = {
  gameRecordId: string
}

/**
 * 在當前牌局 目前玩家的身份
 * @param OWNER                      房主
 * @param PLAYER                     參賽者
 * @param SPECTATOR                  觀戰者
 */
export enum UserIdentity {
  OWNER = 'owner',
  PLAYER = 'player',
  SPECTATOR = 'spectator',
}

export type GameRecordType = {
  top: UserInfoType
  right: UserInfoType
  bottom: UserInfoType
  left: UserInfoType

  dealerState: DealerStateType
  setting: SettingType
  wind: Wind
  round: Round
  status: GameRecordStatus

  owner: string
  ownerPositionName: Round
  users: string[]

  record: RecordType[]
  createAt?: Date
  endAt?: Date
}

export type UserInfoType = {
  name: string
  amount: number
}

export type DealerStateType = {
  winningStreakCount: number
  position: Position
}

export type SettingType = {
  base: number
  bonus: number
  startDealerPosition: Position
}

export enum GameRecordStatus {
  PREPARING = 'preparing',
  PLAYING = 'playing',
  FINISH = 'finish',
}
