//@ts-nocheck
import { useEffect, useMemo } from 'react'
import { UserStateStore, RecordType } from '../../../store/userStateStore'
import clsx from 'clsx'

const positionArray = ['top', 'right', 'bottom', 'left']

type RecordType = {
  record: any[]
  userState: any
}

const Record = ({ record, userState }: RecordType): JSX.Element => {
  // const { record, userState } = UserStateStore((state) => ({
  //   record: state.record,
  //   userState: state.userState,
  // }))

  const newRecord = [].concat(record)
  newRecord.reverse()

  const moneyColor = (number) => {
    let color
    if (number > 0) {
      color = 'text-[#009900]'
    } else if (number < 0) {
      color = 'text-[#ff0000]'
    } else {
      color = 'text-[#000]'
    }
    return color
  }

  // 玩家名稱 及 總結金額顯示
  const UserBox = ({ position, index }): JSX.Element => {
    return (
      <div
        key={position}
        className={clsx(
          index % 2 === 0 ? 'bg-[#f7eee1]' : 'bg-[#FFFCEC]',
          'w-[21.25%] flex flex-col items-center justify-center text-lg pb-5 px-2 pt-4'
        )}
      >
        <div className="w-full text-center text-ellipsis whitespace-nowrap overflow-hidden ">
          {userState[position].name}
        </div>
        <div className={clsx('font-bold', moneyColor(totalAmount[position]))}>{totalAmount[position]}</div>
      </div>
    )
  }

  const totalAmount = useMemo(() => {
    const res = {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    }

    record.length &&
      record.forEach((item) => {
        positionArray.forEach((position: string) => {
          res[position] += item[position].amount
        })
      })
    return res
  }, [record.length])

  if (record.length === 0) {
    return (
      <div className="recordContentHeight flex items-center justify-center">
        <span className="text-2xl">請先開始牌局</span>
      </div>
    )
  } else {
    return (
      <div className="recordContentHeight px-2">
        {/* Header */}
        <div className="flex items-stretch sticky top-0 z-10">
          <div className="w-[15%] bg-[#FFFCEC]"></div>
          {positionArray.map((position: string, index) => {
            return <UserBox key={`${position}_${index}`} position={position} index={index} />
          })}
        </div>
        {/* Body */}
        <div>
          {newRecord.map((item: RecordType, recordIndex) => {
            return (
              <div key={`${item?.wind}_${item?.round}_${recordIndex}`} className="flex items-stretch justify-start">
                <div className="w-[15%] px-1 text-base flex flex-col items-center text-center">
                  <div className="w-[45%] py-3 bg-[white] rounded-xl overflow-hidden text-[brown] shadow-[0px_4px_12px_rgba(0,0,0,0.1)] mb-2">
                    {newRecord.length === recordIndex + 1 ||
                    newRecord[recordIndex + 1].round !== newRecord[recordIndex].round ? (
                      item?.round
                    ) : (
                      <span className="text-3xl">&bull;</span>
                    )}
                  </div>
                  {(newRecord.length === recordIndex + 1 ||
                    newRecord[recordIndex + 1].wind !== newRecord[recordIndex].wind) && (
                    <div className="w-full mt-[50px] mb-5 bg-[white] rounded-xl overflow-hidden text-[brown] shadow-[0px_4px_12px_rgba(0,0,0,0.1)]">
                      {item?.wind}圈
                    </div>
                  )}
                </div>
                {positionArray.map((position: string, index) => {
                  return (
                    <div
                      key={`${position}_${index}`}
                      className={clsx(
                        'flex flex-col items-center justify-start w-[21.25%] px-1 pb-5',
                        index % 2 === 0 ? 'bg-[#f7eee1]' : 'bg-[#FFFCEC]'
                      )}
                    >
                      <div className={clsx('text-base mb-2', moneyColor(item[position].amount))}>
                        {item[position].amount}
                      </div>
                      {item.winnerPosition === item.loserPosition ? (
                        position === item.winnerPosition ? (
                          <div className="text-sm bg-[green] rounded-xl overflow-hidden text-white w-full text-center mb-2">
                            自摸{item.bonus}台
                          </div>
                        ) : null
                      ) : position === item.winnerPosition ? (
                        <div className="text-sm bg-[green] rounded-xl overflow-hidden text-white w-full text-center mb-2">
                          胡{item.bonus}台
                        </div>
                      ) : position === item.loserPosition ? (
                        <div className="text-sm bg-[#ff0000bd] text-white rounded-xl overflow-hidden w-full text-center mb-2">
                          放槍
                        </div>
                      ) : null}
                      {item[position].isDealer && (
                        <div
                          className={clsx(
                            'text-sm mb-2 bg-[orange] text-[white] rounded-xl overflow-hidden w-full text-center'
                          )}
                        >
                          {item[position].winningStreakCount > 0 ? `連${item[position].winningStreakCount}` : '莊'}
                        </div>
                      )}
                      {item.timer > 1 && position === item.winnerPosition && (
                        <div
                          className={clsx(
                            'text-sm rounded-xl overflow-hidden w-full text-center mb-2 bg-blue-300 text-blue-700'
                          )}
                        >
                          {item.timer}倍
                        </div>
                      )}
                    </div>
                  )
                })}
              </div>
            )
          })}
        </div>
      </div>
    )
  }
}

export default Record
