import create from 'zustand'

/**
 * 當前牌局設定及牌局狀態
 * @param isLoading                 讀取API狀態
 */

interface InitLayoutStoreStateType {
  isLoading: boolean
}

export interface LayoutStoreStateType {
  isLoading: boolean
  setIsLoading: (isLoading: boolean) => void
}

const init: InitLayoutStoreStateType = {
  isLoading: false,
}

export const layoutStore = create<LayoutStoreStateType>((set, get) => ({
  ...init,
  setIsLoading: (isLoading) => {
    set({
      isLoading: isLoading,
    })
  },
}))
