import { ReactNode } from 'react'
import clsx from 'clsx'

type LayoutProps = {
    children?: ReactNode
    className?: string
}

export const Layout = ({children, className}: LayoutProps) :JSX.Element => {
    return (
        <section className={clsx("bg-[#FFFCEC] vh100", className)} >
            {children}
        </section>
    )
}