import { useEffect, useState } from 'react'
import { Layout } from '../../component/Element'
import { login, getUserProfile, register as registerAPI } from '../../api'
import { useNavigate } from 'react-router-dom'
import { getCookie } from '../../function'
import clsx from 'clsx'
import { ReactComponent as ArrowLeft } from '../../assets/ic_arrow_left.svg'
import { Toast } from 'antd-mobile'
import { layoutStore, LayoutStoreStateType } from '../../store/LayoutStore'

/**
 * 連線模式 - 註冊頁面
 */

export const Register = (): JSX.Element => {
  const navigate = useNavigate()
  const token = getCookie('token')

  const { setIsLoading } = layoutStore((state: LayoutStoreStateType) => ({
    setIsLoading: state.setIsLoading,
  }))

  // 攜帶重新導向的 賽局ID
  const gameRecordId = window.location.href.split('?gameRecordId=')[1] || ''

  const ruleTwPhone = RegExp(/^09\d{8}$/)
  const pattern = /[!@#$%^&*()_+\{\}:“<>?,.\/;'\[\]\\|`~]+/g
  const rulePassword = RegExp(/[a-zA-Z0-9]{6,16}$/)

  const [data, setData] = useState({
    name: '',
    phone: '',
    password: '',
    secondPassword: '',
  })

  const onChange = (key: string, value: string) => {
    let newValue = value

    newValue = newValue.replace(/[, ]/g, '')
    newValue = newValue.replace(pattern, '')

    switch (key) {
      case 'phone':
        newValue = newValue.replace(/[^\d]/g, '').slice(0, 10)
        break
    }

    setData({
      ...data,
      [key]: newValue,
    })
  }

  const doRegister = async () => {
    if (!data.name || !data.phone || !data.password || !data.secondPassword) {
      Toast.show({
        content: <div className="">請填寫所有欄位</div>,
        position: 'center',
      })
      return
    }

    // 暱稱長度判斷
    if (data.name.length > 15) {
      Toast.show({
        content: <div className="">暱稱太長(最多15字)</div>,
        position: 'center',
      })
      return
    }

    // 手機號碼格式錯誤(台灣)
    if (!ruleTwPhone.test(data.phone)) {
      Toast.show({
        content: <div className="">手機號碼格式錯誤(台灣)</div>,
        position: 'center',
      })
      return
    }

    // 密碼至少要有6碼
    if (data.password.length < 6) {
      Toast.show({
        content: <div className="">密碼至少要有6碼</div>,
        position: 'center',
      })
      return
    }

    // 驗證密碼格式
    if (!rulePassword.test(data.password)) {
      Toast.show({
        content: <div className="">密碼僅能輸入"英文、數字" (6~16碼)</div>,
        position: 'center',
      })
      return
    }

    // 兩組密碼 是否一致
    if (data.password !== data.secondPassword) {
      Toast.show({
        content: <div className="">兩組密碼不一致</div>,
        position: 'center',
      })
      return
    }

    setIsLoading(true)
    await registerAPI({
      name: data.name,
      phone: data.phone,
      password: data.password,
    })
      .then(async (res) => {
        // 判斷是否要重導向回 該牌局中
        if (res?.success) {
          Toast.show({
            content: <div className="">註冊成功</div>,
            position: 'center',
          })
          await doLogin()
        } else {
          Toast.show({
            content: <div className="">註冊失敗</div>,
            position: 'center',
          })
        }
      })
      .finally(() => {
        setIsLoading(false)
      })
  }

  const doLogin = async () => {
    if (!data.phone || !data.password) return
    setIsLoading(true)
    await login(data)
      .then((res) => {
        if (res?.success) {
          console.log('登入成功')
          document.cookie = `token=${res?.token};max-age=2592000`

          // 判斷是否要重導向回 該牌局中
          if (gameRecordId) {
            navigate(`/online/${gameRecordId}`)
          } else {
            navigate('/menu')
          }
        } else {
          console.log('登入失敗:')
        }
      })
      .finally(() => {
        setIsLoading(false)
      })
  }

  useEffect(() => {
    if (token) {
      setIsLoading(true)

      getUserProfile()
        .then((res) => {
          if (res.success) {
            navigate('/menu')
          } else {
            document.cookie = `token=;max-age=0`
          }
        })
        .finally(() => {
          setIsLoading(false)
        })
    }
  }, [])

  return (
    <Layout className="flex flex-col justify-between items-center">
      {/* Header */}
      <div className="h-[50px] flex justify-start items-center w-full text-base">
        <div className={clsx('flex flex-col justify-center items-center w-1/3')} onClick={() => navigate('/')}>
          <span className="font-bold flex justify-center items-center h-[24px] w-[24px]">
            <ArrowLeft />
          </span>
          <span>返回登入</span>
        </div>
      </div>
      {/* Content */}
      <div className="flex flex-col justify-center items-center w-[80%]">
        <div className="text-3xl font-bold mb-20">註冊</div>
        <div className="bg-white flex flex-col px-4 py-6 w-full">
          <div className="flex items-center text-lg mb-5">
            <div className="mr-5 w-[40%]">
              <span className="text-[red]">*</span>暱稱:
            </div>
            <input
              type="text"
              className="border-[1px] border-solid"
              value={data.name}
              onChange={(e) => {
                onChange('name', e.target.value)
              }}
              style={{
                width: '50%',
              }}
            />
          </div>
          <div className="flex items-center text-lg mb-5 w-full">
            <div className="mr-5 w-[40%]">
              <span className="text-[red]">*</span>電話:
            </div>
            <input
              type="number"
              maxLength={10}
              className="border-[1px] border-solid"
              value={data.phone}
              onChange={(e) => {
                onChange('phone', e.target.value)
              }}
              style={{
                width: '50%',
              }}
            />
          </div>
          <div className="flex items-center text-lg mb-5">
            <div className="mr-5 w-[40%]">
              <span className="text-[red]">*</span>密碼:
            </div>
            <input
              type="password"
              className="border-[1px] border-solid"
              value={data.password}
              onChange={(e) => {
                onChange('password', e.target.value)
              }}
              style={{
                width: '50%',
              }}
            />
          </div>
          <div className="flex items-center text-lg mb-5">
            <div className="mr-5 w-[40%]">
              <span className="text-[red]">*</span>重複密碼:
            </div>
            <input
              type="password"
              className="border-[1px] border-solid"
              value={data.secondPassword}
              onChange={(e) => {
                onChange('secondPassword', e.target.value)
              }}
              style={{
                width: '50%',
              }}
            />
          </div>
          <div className="w-full flex justify-center items-center text-xl">
            <div onClick={doRegister} className="bg-[#455ee8] text-white rounded px-6 py-1">
              註冊
            </div>
          </div>
        </div>
      </div>

      {/* Footer */}
      <div></div>
    </Layout>
  )
}
