import create from 'zustand'

/**
 * 當前牌局設定及牌局狀態
 * @param id                        玩家id
 * @param name                      玩家名稱
 * @param currentGameRecordId       當前牌局id
 * @param phone                     手機號碼(帳號)
 */

interface InitUserStoreStateType {
  name: string
  currentGameRecordId: string
  phone: string
  id: string
}

export interface UserStoreStateType {
  id: string
  name: string
  currentGameRecordId: string
  phone: string
  setUserStoreState: (userState: InitUserStoreStateType) => void
}

const init: InitUserStoreStateType = {
  id: '',
  name: '',
  currentGameRecordId: '',
  phone: '',
}

export const userStore = create<UserStoreStateType>((set, get) => ({
  ...init,
  setUserStoreState: (userState) => {
    set({
      name: userState.name,
      currentGameRecordId: userState.currentGameRecordId,
      phone: userState.phone,
      id: userState.id,
    })
  },
}))
