import { useNavigate } from 'react-router-dom'
import { Layout } from '../../component/Element'
import { useEffect, useState } from 'react'
import { getUserProfile, postGameRecord } from '../../api'
import { userStore, UserStoreStateType } from '../../store/userStore'
import { getCookie } from '../../function'
import { Button, Modal, Space, Toast, Divider, Picker, NumberKeyboard, Switch } from 'antd-mobile'
import clsx from 'clsx'
import { Wind } from '../../types'
import { layoutStore, LayoutStoreStateType } from '../../store/LayoutStore'
import { ReactComponent as ArrowLeft } from '../../assets/ic_arrow_left.svg'

const positionArray = [
  [
    { label: Wind.東, value: Wind.東 },
    { label: Wind.南, value: Wind.南 },
    { label: Wind.西, value: Wind.西 },
    { label: Wind.北, value: Wind.北 },
  ],
]

/**
 * 連線模式 - 主選單
 */
export const Menu = (): JSX.Element => {
  const navigate = useNavigate()
  const token = getCookie('token')

  const { setUserStoreState, currentGameRecordId } = userStore((state: UserStoreStateType) => ({
    setUserStoreState: state.setUserStoreState,
    currentGameRecordId: state.currentGameRecordId,
  }))

  const { setIsLoading } = layoutStore((state: LayoutStoreStateType) => ({
    setIsLoading: state.setIsLoading,
  }))

  const [ownerPositionName, setOwnerPositionName] = useState<string>('')

  const [isCreateNewGameModalOpen, setIsCreateNewGameModalOpen] = useState(false)

  const goPage = (url: string) => {
    navigate(url)
  }

  useEffect(() => {
    if (token) {
      getUserProfile().then((res) => {
        if (res.success) {
          console.log('登入成功')
          setUserStoreState(res.data)
        } else {
          goPage('/')
          document.cookie = `token=;max-age=0`
        }
      })
    } else {
      goPage('/')
    }
  }, [])

  const createNewGame = () => {
    if (!ownerPositionName) return
    setIsLoading(true)
    postGameRecord({
      ownerPositionName: ownerPositionName,
    }).then((res) => {
      if (res.success) {
        navigate(`/online/${res.data._id}`)
        setIsLoading(false)
      } else {
        console.log('建立牌局失敗')
        Toast.show({
          content: <div className="">建立牌局失敗</div>,
          position: 'top',
        })
        setIsLoading(false)
      }
    })
  }

  return (
    <Layout className="flex flex-col justify-between items-center">
      {/* Header */}
      <div className="h-[50px] flex justify-start items-center w-full text-base">
        <div className={clsx('flex flex-col justify-center items-center w-1/3')} onClick={() => navigate('/')}>
          <span className="font-bold flex justify-center items-center h-[24px] w-[24px]">
            <ArrowLeft />
          </span>
          <span>返回首頁</span>
        </div>
      </div>
      {/* content */}
      <div className="w-full flex flex-col justify-center items-center">
        <div className="mb-[20%] text-3xl">連線模式</div>
        <div className="w-[80%] bg-white flex flex-col rounded px-6 py-10 text-lg">
          {currentGameRecordId ? (
            <div
              className="rounded px-2 py-1 text-center border-[1px] border-black border-solid mb-8"
              onClick={() => {
                goPage(`/online/${currentGameRecordId}`)
              }}
            >
              當前牌局
            </div>
          ) : (
            <div
              className="rounded px-2 py-1 text-center border-[1px] border-black border-solid mb-8"
              onClick={() => {
                setIsCreateNewGameModalOpen(true)
              }}
            >
              開始新牌局
            </div>
          )}
          <div
            className="rounded px-2 py-1 text-center border-[1px] border-black border-solid"
            //   onClick={() => goPage('/login')}
          >
            過往牌局紀錄
          </div>
        </div>
      </div>

      {/* Footer */}
      <div></div>

      {/* 建立新牌局 Modal */}
      <div
        className={clsx(
          'vh100 absolute top-0 left-0 w-[100vw] bg-black-1/10 z-[5] justify-center items-center',
          isCreateNewGameModalOpen ? 'flex' : 'hidden'
        )}
        onClick={() => setIsCreateNewGameModalOpen(false)}
      >
        <div className="z-10 w-[80vw] p-4 bg-[#fff] rounded-3xl" onClick={(e) => e.stopPropagation()}>
          <div className="mb-10 text-center text-2xl font-bold">位置選擇</div>
          <div className="mb-10 text-xl">
            <div className="flex mb-3">
              <div className="w-1/2 flex justify-center items-center">位置</div>
              <div className="w-1/2 flex justify-center items-center">
                <Picker
                  columns={positionArray}
                  value={[ownerPositionName]}
                  onConfirm={(e) => setOwnerPositionName(e[0] as string)}
                  onSelect={(val, extend) => {}}
                >
                  {(items, { open }) => {
                    return (
                      <Button className="!py-[5px] !px-[20px] !border-0 !bg-blue-400 !text-[#fff]" onClick={open}>
                        {items.every((item) => item === null)
                          ? '未选择'
                          : items.map((item) => item?.label ?? '未选择').join(' - ')}
                      </Button>
                    )
                  }}
                </Picker>
              </div>
            </div>
          </div>
          <div className="flex justify-center items-center relative top-0">
            <div
              className="flex justify-center items-center bg-blue-600 text-[#fff] py-2 px-12 rounded text-xl"
              onClick={createNewGame}
            >
              建立新牌局
            </div>
          </div>
        </div>
      </div>
    </Layout>
  )
}
