import { UserIdentity, BaseResponse, GameRecordType, Position, Wind, GameRecordStatus } from '../../types'
import {
  getUserProfile,
  getGameRecord,
  postEndGame,
  postStartGame,
  postAddNewUser,
  postRecord,
  postNoOneWinTheGame,
} from '../../api'
