/**
 * 連線模式 - 當前牌局
 */

const baseArray = [
  [
    { label: '10', value: 10 },
    { label: '20', value: 20 },
    { label: '30', value: 30 },
    { label: '40', value: 40 },
    { label: '50', value: 50 },
  ],
]

const positionArray = [Position.TOP, Position.RIGHT, Position.BOTTOM, Position.LEFT]

const windArray = [Wind.東, Wind.南, Wind.西, Wind.北]

// enum Position {
//   TOP = 'top',
//   RIGHT = 'right',
//   BOTTOM = 'bottom',
//   LEFT = 'left',
// }

type PositionType = Position.TOP | Position.RIGHT | Position.BOTTOM | Position.LEFT | ''

/**
 * 玩家狀態
 * @param name                      玩家名稱
 * @param count                     輸贏金額
 * @param isDealer                  是否為莊家
 * @param winningStreakCount        連莊次數
 */
type UserType = {
  name: string
  count: number
  isDealer: boolean
  winningStreakCount: number
}

type DealerStateType = {
  name: string
  winningStreakCount: number
}

type positionName = { [key: string]: string }

export const Online = (): JSX.Element => {
  const navigate = useNavigate()
  const token = getCookie('token')

  const { gameRecordId } = useParams()

  const { setIsLoading } = layoutStore((state: LayoutStoreStateType) => ({
    setIsLoading: state.setIsLoading,
  }))

  const { id, setUserStoreState, currentGameRecordId } = userStore((state: UserStoreStateType) => ({
    id: state.id,
    setUserStoreState: state.setUserStoreState,
    currentGameRecordId: state.currentGameRecordId,
  }))

  const [gameRecord, setGameRecord] = useState<GameRecordType>()
  const [identity, setIdentity] = useState<UserIdentity>()
  const [positionNameMap, setPositionNameMap] = useState<positionName>()

  // 藉由房主位置判斷 目前的 東南西北 相對位置
  const checkPositionName = (gameRecord: GameRecordType) => {
    const currentWindIndex = windArray.indexOf(gameRecord.ownerPositionName)
    const map = {
      top: windArray[(currentWindIndex + 2) % 4],
      right: windArray[(currentWindIndex + 1) % 4],
      bottom: gameRecord.ownerPositionName,
      left: windArray[(currentWindIndex + 3) % 4],
    }
    setPositionNameMap(map)
  }

  // 判斷目前狀態
  // 房主 or 參賽玩家 or 觀戰者

  useEffect(() => {
    console.log(':gameRecordId:', gameRecordId)

    if (gameRecordId) {
      setIsLoading(true)
      // 取得賽局資料
      getGameRecord(gameRecordId).then((res: BaseResponse<GameRecordType>) => {
        if (res.success) {
          setGameRecord(res.data)

          checkPositionName(res.data)

          // setWs(webSocket('http://localhost:3030'))
          // setWs(io('http://localhost:5500'))
          initWebSocket()

          // 當確定有賽局資料後，進行身份判斷
          getProfile(res.data)

          // const ws = socket()
          // ws.emit('addRoom', gameRecordId)
        } else {
          console.log('查無此牌局')
          navigate('/')
        }
      })
    } else {
      // navigate('/')
    }
  }, [gameRecordId])

  const getProfile = (gameRecord) => {
    if (token) {
      // 有登入 進行身份判斷
      getUserProfile()
        .then((res) => {
          if (res.success) {
            console.log('登入成功')
            setUserStoreState(res.data)
            if (gameRecord.owner === res.data.id) {
              // 是房主
              setIdentity(UserIdentity.OWNER)
              console.log('房主')
            } else if (gameRecord.users.indexOf(res.data.id) !== -1) {
              // 其他參賽玩家
              setIdentity(UserIdentity.PLAYER)
              console.log('參賽玩家')
            } else {
              // 非參賽玩家
              setIdentity(UserIdentity.SPECTATOR)
              console.log('觀戰者')
            }
          } else {
            // 取得玩家資料失敗
            setIdentity(UserIdentity.SPECTATOR)
            console.log('觀戰者')
          }
        })
        .finally(() => {
          setIsLoading(false)
        })
    } else {
      // 沒登入 視為觀戰者
      setIdentity(UserIdentity.SPECTATOR)
      console.log('觀戰者')
      setIsLoading(false)
    }
  }

  useEffect(() => {
    console.log('gameRecordId:', gameRecordId)

    if (!gameRecordId) return
    // 設定 每分鐘 重新撈取最新資料
    console.log('設定 每分鐘 重新撈取最新資料')
    const timer = setInterval(() => {
      getNewGameRecord()
    }, 60 * 1000)

    return () => {
      console.log('清除計時器')
      clearInterval(timer)
    }
  }, [gameRecordId])

  const initWebSocket = () => {
    // console.log('initWebSocket')
    // const ws = socket()
    // //對 getMessage 設定監聽，如果 server 有透過 getMessage 傳送訊息，將會在此被捕捉
    // ws.on('all', (message: any) => {
    //   console.log('all', message)
    // })
    // // 當接受到 後端 websocket 通知時，主動打API取得 最新的牌局資料
    // ws.on(`${gameRecordId}`, (message: any) => {
    //   console.log(`收到後端通知，更新牌局資料`, message)
    //   getNewGameRecord()
    // })
  }

  // websocket 觸發更新時，重拿新的賽局資料
  const getNewGameRecord = () => {
    console.log('取得新的賽局資料')

    // setIsLoading(true)
    getGameRecord(gameRecordId)
      .then((res: BaseResponse<GameRecordType>) => {
        if (res.success) {
          setGameRecord(res.data)
        } else {
          console.log('查無此牌局')
        }
      })
      .finally(() => {
        // setIsLoading(false)
      })
  }

  // 結束牌局
  const endGame = () => {
    setIsLoading(true)
    postEndGame({
      gameRecordId: gameRecordId,
    })
      .then((res) => {
        if (res.success) {
          navigate('/menu')
        } else {
          console.log('結束遊戲失敗')
        }
      })
      .finally(() => {
        setIsLoading(false)
      })
  }

  // 複製此牌局網址
  const copyUrl = () => {
    navigator.clipboard.writeText(window.location.href).then(() => {
      Toast.show({
        content: <div className="">網址已複製</div>,
        position: 'top',
      })
    })
  }

  // 加入此牌局
  const joinGame = () => {
    if (!currentPosition || !gameRecordId) return
    setIsLoading(true)
    postAddNewUser({
      position: currentPosition,
      gameRecordId: gameRecordId,
    })
      .then((res) => {
        if (res.success) {
          Toast.show({
            content: <div className="">成功加入牌局</div>,
            position: 'top',
          })
          window.location.reload()
        } else {
          console.log('加入牌局失敗')
          Toast.show({
            content: <div className="">加入牌局失敗</div>,
            position: 'top',
          })
        }
      })
      .finally(() => {
        setIsLoading(false)
      })
  }

  // 主動送出websocket message 通知後端資料更新了
  const sendWebsocket = () => {
    // const ws = socket()
    // ws.emit('updateGameRecord', gameRecordId)
    getNewGameRecord()
  }

  // 開始牌局
  const startGame = () => {
    setIsLoading(true)
    console.log()

    postStartGame({
      gameRecordId: gameRecordId,
      setting: gameRecord?.setting,
    })
      .then((res) => {
        if (res.success) {
          Toast.show({
            content: <div className="">牌局開始</div>,
            position: 'top',
          })
          // 觸發 websocket 通知 已更新牌局資料
          sendWebsocket()
        } else {
          console.log('開始牌局失敗')
          Toast.show({
            content: <div className="">開始牌局失敗</div>,
            position: 'top',
          })
        }
      })
      .finally(() => {
        setIsLoading(false)
        setIsSettingModalOpen(false)
      })
  }

  // 加入牌局紀錄
  const addRecord = (record) => {
    setIsLoading(true)
    postRecord(record)
      .then((res) => {
        if (res.success) {
          Toast.show({
            content: <div className="">紀錄成功</div>,
            position: 'top',
          })
          // 觸發 websocket 通知 已更新牌局資料
          sendWebsocket()
        } else {
          Toast.show({
            content: <div className="">紀錄失敗</div>,
            position: 'top',
          })
        }
      })
      .finally(() => {
        setIsLoading(false)
        setWinData(initWinData)
        setCurrentPosition('')
      })
  }

  // 流局
  const noOneWinTheGame = () => {
    setIsLoading(true)
    postNoOneWinTheGame(gameRecordId)
      .then((res) => {
        if (res.success) {
          Toast.show({
            content: <div className="">流局成功</div>,
            position: 'top',
          })
          // 觸發 websocket 通知 已更新牌局資料
          sendWebsocket()
        } else {
          Toast.show({
            content: <div className="">流局失敗</div>,
            position: 'top',
          })
        }
      })
      .finally(() => {
        setIsLoading(false)
      })
  }

  //
  //
  //
  //
  //
  //
  //

  // const positionArray = [Position.TOP, Position.RIGHT, Position.BOTTOM, Position.LEFT]

  const {
    userState,
    setUserState,
    dealerState,
    setDealerState,
    win,
    setting,
    setSetting,
    wind,
    round,
    // startNewGame,
    // noOneWinTheGame,
    // addRecord,
  } = UserStateStore((state) => ({
    userState: state.userState,
    setUserState: state.setUserState,
    dealerState: state.dealerState,
    setDealerState: state.setDealerState,
    win: state.win,
    setting: state.setting,
    setSetting: state.setSetting,
    wind: state.wind,
    round: state.round,
    startNewGame: state.startNewGame,
    noOneWinTheGame: state.noOneWinTheGame,
    addRecord: state.addRecord,
  }))

  // const [dealerState, setDealerState] = useState<DealerStateType>({
  //   name: "",
  //   winningStreakCount: 0
  // })

  // const [gameRecord, setGameRecord] = useState({})

  // const userCount = useMemo(() => {
  //   const newUserCount = {}
  //   for (const [key, value] of Object.entries(gameRecord)) {
  //     newUserCount[key] = value.reduce((pre, cur, index) => {
  //       return (pre += cur)
  //     }, 0)
  //   }
  //   return newUserCount
  // }, [gameRecord])

  const initWinData = {
    bonus: 0, // 台數
    loserPosition: '', // 輸家
    timer: 1, //  倍率
    haveTimer: false, // 有沒有加倍
  }

  const [isEndGameModalOpen, setEndGameModalOpen] = useState<boolean>(false)
  const [isNoOneWinModalOpen, setIsNoOneWinModalOpen] = useState<boolean>(false)

  const [currentUserName, setCurrentUserName] = useState<any>('')

  const [currentPosition, setCurrentPosition] = useState<PositionType>()

  const [winData, setWinData] = useState(initWinData)

  const onWinDataChange = (key, value) => {
    switch (key) {
      case 'bonusLoser':
        setWinData({
          ...winData,
          bonus: value[0],
          loserPosition: value[1],
        })

        break
      case 'timer':
        setWinData({
          ...winData,
          timer: value[0],
        })
        break
      case 'haveTimer':
        if (!value) {
          setWinData({
            ...winData,
            timer: 1,
            haveTimer: value,
          })
        } else {
          setWinData({
            ...winData,
            timer: 2,
            haveTimer: value,
          })
        }

        break
      default:
        setWinData({
          ...winData,
          [key]: value,
        })
        break
    }
  }

  // 當前牌局 玩家名稱陣列，判斷是否 四位玩家了
  const userArray = useMemo(() => {
    if (!gameRecord) return []
    const array = positionArray.map((position) => {
      return gameRecord[position].name
    })
    return array
  }, [gameRecord])

  // 牌局設定 - 起莊 下拉選單
  const userOption = useMemo(() => {
    if (!gameRecord) return []
    const option = positionArray.map((position) => {
      return {
        label: gameRecord[position].name,
        value: position,
      }
    })
    return option || []
  }, [gameRecord])

  // 玩家名稱是否皆以輸入完成。
  const isReady = userArray.every((name) => name !== '')

  const [checkIsWin, setCheckIsWin] = useState<boolean>(false)

  const onClickUserBox = (position: PositionType) => {
    // 如果點擊的位置 已經有玩家 將無法顯示彈窗
    if (currentPosition === position) {
      setCurrentPosition('')
    } else {
      if (
        gameRecord?.status === GameRecordStatus.PREPARING &&
        identity === UserIdentity.SPECTATOR &&
        !gameRecord[position].name
      ) {
        // 準備中 僅有 觀戰者 點擊 空的位置 才會觸發
        setCurrentPosition(position)
        // setCurrentUserName(userState[position].name)
      } else if (gameRecord?.status === GameRecordStatus.PLAYING && identity === UserIdentity.OWNER) {
        // 遊玩中 僅有 房主 點擊 才會觸發
        setCurrentPosition(position)
        // setCurrentUserName(userState[position].name)
        setCheckIsWin(false)
      }

      // setCurrentPosition(position)
      // setCurrentUserName(userState[position].name)

      // if (gameRecord?.status === GameRecordStatus.PLAYING) {
      //   setCheckIsWin(false)
      // } else {
      //   // 其他玩家 點擊加入時
      // }
    }
  }

  const onAddRecord = () => {
    // const newUserState = { ...userState }

    const record = {
      gameRecordId: gameRecordId,
      round: gameRecord?.round,
      wind: gameRecord?.wind,
      timer: winData.timer,
      bonus: winData.bonus,
      winnerPosition: currentPosition,
      loserPosition: winData.loserPosition,
      top: {},
      right: {},
      bottom: {},
      left: {},
    }

    // 是不是自摸
    const isWinThree = winData?.loserPosition === currentPosition

    // 總金額
    let total
    // 莊的台數
    const dealerCount = 1 + gameRecord?.dealerState?.winningStreakCount * 2
    // 基本費用 （底＋台，沒包含莊家）
    const baseFee = gameRecord?.setting?.base + gameRecord?.setting?.bonus * winData?.bonus
    // 莊家輸的錢
    let dealerLoseCount
    // 一般玩家輸的錢
    let loseCount

    if (isWinThree) {
      // 自摸
      if (currentPosition === gameRecord?.dealerState.position) {
        total = (baseFee + dealerCount * gameRecord?.setting?.bonus) * 3 * winData?.timer
        // 莊家自摸
      } else {
        total = (baseFee * 3 + dealerCount * gameRecord?.setting?.bonus) * winData?.timer
        dealerLoseCount = (baseFee + dealerCount * gameRecord?.setting?.bonus) * winData?.timer
        loseCount = baseFee * winData?.timer
      }
    } else {
      // 胡牌
      if (
        gameRecord?.dealerState.position === currentPosition ||
        winData.loserPosition === gameRecord?.dealerState.position
      ) {
        // 有莊家台
        total = (baseFee + dealerCount * gameRecord?.setting?.bonus) * winData?.timer
      } else {
        total = baseFee * winData?.timer
      }
    }

    // 計算各家金額
    positionArray.forEach((position: string) => {
      // let newRecord = [].concat(newUserState[position].record)

      if (isWinThree) {
        // 自摸
        if (currentPosition === gameRecord?.dealerState.position) {
          // 莊家自摸
          if (currentPosition === position) {
            // 自摸的人
            // newRecord.push(total)
            record[position].amount = total
          } else {
            // 輸的人
            // newRecord.push(0 - total / 3)
            record[position].amount = 0 - total / 3
          }
        } else {
          // 其他人自摸
          if (currentPosition === position) {
            // 自摸的人
            // newRecord.push(total)
            record[position].amount = total
          } else if (position === gameRecord?.dealerState.position) {
            // 輸的莊家
            // newRecord.push(0 - dealerLoseCount)
            record[position].amount = 0 - dealerLoseCount
          } else {
            // 其他輸的人
            // newRecord.push(0 - loseCount)
            record[position].amount = 0 - loseCount
          }
        }
      } else {
        // 胡牌
        if (currentPosition === position) {
          // 胡的人
          // newRecord.push(total)
          record[position].amount = total
        } else if (winData.loserPosition === position) {
          // 放槍的人
          // newRecord.push(0 - total)
          record[position].amount = 0 - total
        } else {
          // 其他人
          // newRecord.push(0)
          record[position].amount = 0
        }
      }

      // newUserState[position].record = newRecord

      record[position].isDealer = gameRecord?.dealerState.position === position
      record[position].winningStreakCount = record[position].isDealer ? gameRecord?.dealerState.winningStreakCount : 0
    })

    // setUserState(newUserState)
    // win(currentPosition)

    // setCurrentPosition('')
    // setWinData(initWinData)

    console.log('record:', record)

    // 寫入這局紀錄
    addRecord(record)
  }

  const ResultBox = (): JSX.Element => {
    // 是不是自摸
    const isWinThree = winData?.loserPosition === currentPosition

    // 總金額
    let total
    // 莊的台數
    const dealerCount = 1 + gameRecord?.dealerState?.winningStreakCount * 2
    // 顯示自摸 及 莊台的方式
    let Box

    if (isWinThree) {
      // 是'莊家'自摸
      if (currentPosition === gameRecord?.dealerState?.position) {
        total =
          (gameRecord?.setting?.base +
            gameRecord?.setting?.bonus * winData?.bonus +
            dealerCount * gameRecord?.setting?.bonus) *
          3 *
          winData?.timer
        Box = () => {
          return (
            <>
              <div className="w-[1/5]">
                <div className="text-center">莊{dealerCount}台</div>
                <div className="text-center">{dealerCount * gameRecord?.setting?.bonus}</div>
              </div>
              <div className="w-[1/5]">
                <div className="text-center">自摸</div>
                <div className="text-center">x 3</div>
              </div>
            </>
          )
        }
      } else {
        total =
          ((gameRecord?.setting?.base + gameRecord?.setting?.bonus * winData?.bonus) * 3 +
            dealerCount * gameRecord?.setting?.bonus) *
          winData?.timer
        Box = () => {
          return (
            <>
              <div className="w-[1/5]">
                <div className="text-center">自摸</div>
                <div className="text-center">x 3</div>
              </div>
              <div className="w-[1/5]">
                <div className="text-center">莊{dealerCount}台</div>
                <div className="text-center">{dealerCount * gameRecord?.setting?.bonus}</div>
              </div>
            </>
          )
        }
      }
    } else {
      // 有莊家台
      if (
        winData?.loserPosition === gameRecord?.dealerState?.position ||
        currentPosition === gameRecord?.dealerState?.position
      ) {
        total =
          (gameRecord?.setting?.base +
            gameRecord?.setting?.bonus * winData?.bonus +
            dealerCount * gameRecord?.setting?.bonus) *
          winData?.timer
        Box = () => {
          return (
            <div className="w-[1/5]">
              <div className="text-center">莊{dealerCount}台</div>
              <div className="text-center">{dealerCount * gameRecord?.setting?.bonus}</div>
            </div>
          )
        }
      } else {
        total = (gameRecord?.setting?.base + gameRecord?.setting?.bonus * winData?.bonus) * winData?.timer
        Box = () => {
          return null
        }
      }
    }

    return (
      <div className="w-full flex justify-around text-base text-gray-500 border-t-[1px] border-gray-300 border-solid pt-3 mt-2">
        <div className="w-[75%] flex justify-around border-r-[1px] border-solid flex-wrap">
          <div className="w-[1/5]">
            <div className="text-center">底</div>
            <div className="text-center">{gameRecord?.setting?.base}</div>
          </div>
          <div className="w-[1/5]">
            <div className="text-center">牌型{winData?.bonus}台</div>
            <div className="text-center">{gameRecord?.setting?.bonus * winData?.bonus}</div>
          </div>
          {Box()}
          {winData?.haveTimer && (
            <div className="w-[1/5]">
              <div className="text-center">倍率</div>
              <div className="text-center">x{winData?.timer}</div>
            </div>
          )}
        </div>

        <div className="w-[25%]">
          <div className="text-center">總金額</div>
          <div className="text-center">{total}</div>
        </div>
      </div>
    )
  }

  const moneyColor = (number) => {
    let color
    if (number > 0) {
      color = '#009900'
    } else if (number < 0) {
      color = '#ff0000'
    } else {
      color = '#000'
    }
    return color
  }

  const UserBox = (position: PositionType): JSX.Element => {
    let positionStyle = ''

    const sizeStyle = () => {
      if (currentPosition === position && gameRecord?.status === GameRecordStatus.PLAYING && checkIsWin) {
        return ' !w-[85vw] !h-[auto]'
      } else if (currentPosition === position && gameRecord?.status === GameRecordStatus.PLAYING && !checkIsWin) {
        return ' !w-[85vw] !h-[40vw]'
      } else if (currentPosition === position) {
        return ' !w-[85vw] !h-[75vw]'
      }
    }

    switch (position) {
      case Position.TOP:
        positionStyle += `top-[20%] left-[50%] translate-x-[-50%] translate-y-[-20%] ${
          currentPosition === position && '!top-[50%] !left-[50%] !translate-x-[-50%] !translate-y-[-50%] z-10'
        }`
        break
      case Position.RIGHT:
        positionStyle += `top-[50%] left-[99%] translate-x-[-99%] translate-y-[-50%] ${
          currentPosition === position && '!top-[50%] !left-[50%] !translate-x-[-50%] !translate-y-[-50%] z-10'
        }`
        break
      case Position.BOTTOM:
        positionStyle += `top-[80%] left-[50%] translate-x-[-50%] translate-y-[-80%] ${
          currentPosition === position && '!top-[50%] !left-[50%] !translate-x-[-50%] !translate-y-[-50%] z-10'
        }`
        break
      case Position.LEFT:
        positionStyle += `top-[50%] left-[1%] translate-x-[-1%] translate-y-[-50%] ${
          currentPosition === position && '!top-[50%] !left-[50%] !translate-x-[-50%] !translate-y-[-50%] z-10'
        }`
        break
    }

    return (
      <div
        key={position}
        className={clsx(
          'userBox absolute rounded-3xl w-[30vw] h-[35vw] bg-[#fff] shadow-[0px_4px_12px_rgba(0,0,0,0.1)]',
          positionStyle,
          sizeStyle()
        )}
        onClick={() => {
          !currentPosition && onClickUserBox(position)
        }}
      >
        {currentPosition === position ? (
          // 被點擊時顯示
          gameRecord?.status === GameRecordStatus.PLAYING ? (
            // 牌局進行中
            checkIsWin ? (
              <div className="py-4 px-2 w-full">
                <div className="font-bold text-2xl mb-3 text-center">胡牌！</div>
                <div className="flex justify-around w-full">
                  <div className="text-xl">牌型台數</div>
                  <div className="text-xl">放槍者</div>
                </div>
                <PickerView
                  className="mb-2"
                  columns={winOptions}
                  value={[winData?.bonus, winData?.loserPosition]}
                  style={{ '--height': '80px', '--item-height': '1.5rem' }}
                  onChange={(val, extend) => {
                    onWinDataChange('bonusLoser', val)
                  }}
                />
                <div className="flex justify-center items-center">
                  <div className="text-xl mr-4">倍率</div>
                  <Switch
                    checked={winData?.haveTimer}
                    onChange={(val) => {
                      onWinDataChange('haveTimer', val)
                    }}
                  />
                </div>
                {winData?.haveTimer && (
                  <PickerView
                    className="mb-2"
                    columns={[timerOptions]}
                    value={[winData?.timer]}
                    style={{ '--height': '80px', '--item-height': '1.5rem' }}
                    onChange={(val, extend) => {
                      onWinDataChange('timer', val)
                    }}
                  />
                )}
                {ResultBox()}
                <div className="flex justify-center items-center">
                  <div className=" px-8 py-1 text-lg rounded bg-[#dff2df] text-[green] mt-4" onClick={onAddRecord}>
                    確定
                  </div>
                </div>
              </div>
            ) : (
              <div className="py-4 flex flex-col justify-center items-center">
                <div className="text-3xl mb-6">請選擇</div>
                <div className="flex">
                  <div
                    className="bg-[#dff2df] px-8 py-2 text-xl rounded text-[#89ba89]"
                    onClick={(e) => {
                      setCheckIsWin(true)
                      e.stopPropagation()
                    }}
                  >
                    胡牌
                  </div>
                </div>
              </div>
            )
          ) : (
            // 空位 - 等待玩家加入
            <div
              className="p-4 flex flex-col items-center justify-between h-full"
              onClick={(e) => {
                e.stopPropagation()
              }}
            >
              {id ? (
                <>
                  <div className="text-center text-2xl font-[400]">
                    您選擇的位置是
                    <br />
                    <span className="text-[red] text-4xl">{positionNameMap[currentPosition]}</span>
                  </div>
                  <div className=" text-2xl font-[400]"> 確定要加入此牌局？</div>
                </>
              ) : (
                <>
                  <div></div>
                  <div className="mb-7 text-center text-2xl font-[400]">請先進行登入</div>
                </>
              )}
              <div className="w-full flex justify-around items-center text-lg">
                <div className="py-1 text-[#929292]" onClick={() => setCurrentPosition('')}>
                  取消
                </div>
                <div
                  className="py-1 font-bold text-[green]"
                  onClick={() => {
                    if (id) {
                      joinGame()
                    } else {
                      navigate(`/login?gameRecordId=${gameRecordId}`)
                    }
                  }}
                >
                  {id ? '加入' : '前往登入'}
                </div>
              </div>
            </div>
          )
        ) : (
          // 正常顯示的狀態 (沒有被點擊)
          <div className="h-full">
            {gameRecord[position].name ? (
              // 有玩家時
              <div className="flex flex-col h-full">
                <div className="flex justify-center items-center h-[30%]">
                  {gameRecord?.dealerState.position === position && (
                    <div className="bg-[orange] p-1 rounded text-[white]">
                      {gameRecord?.dealerState.winningStreakCount === 0
                        ? '莊'
                        : `連${gameRecord?.dealerState.winningStreakCount}`}
                    </div>
                  )}
                  {gameRecord?.owner === gameRecord[position]?.id && <div className="text-[brown] ml-2">房主</div>}
                </div>
                <div className="text-center h-[25%] text-xl text-[#919090] text-ellipsis whitespace-nowrap overflow-hidden">
                  {gameRecord[position].name}
                </div>
                <div
                  className={clsx('flex justify-center items-center h-[45%] text-4xl')}
                  style={{
                    color: moneyColor(gameRecord[position]?.amount || 0),
                  }}
                >
                  {gameRecord[position]?.amount || 0}
                </div>
              </div>
            ) : (
              // 位置是空的時候
              <div className="flex flex-col justify-center h-full">
                <div className="flex justify-center items-center h-[30%] text-[#eed15f] text-xl">
                  {positionNameMap && positionNameMap[position]}
                </div>
                <div className="flex justify-center items-center h-[40%] text-[#919090] text-base text-center">
                  點選你的位置
                  <br />
                  加入牌局
                </div>
                <div className="text-center h-[30%]  text-[green] text-2xl font-bold">＋</div>
              </div>
            )}
          </div>
        )}
      </div>
    )
  }

  // 牌局設定彈窗
  const [isSettingModalOpen, setIsSettingModalOpen] = useState<boolean>(false)

  const onSettingChange = (key, value) => {
    setGameRecord({
      ...gameRecord,
      setting: {
        ...gameRecord?.setting,
        [key]: value,
      },
    })
  }

  // // 開始牌局
  // const startGame = () => {
  //   // 確認開始莊家是否有選擇
  //   if (!setting?.startDealerPosition) {
  //     Toast.show({
  //       content: '請選擇起莊玩家',
  //       position: 'top',
  //     })
  //   } else {
  //     setIsSettingModalOpen(false)
  //     // 設定起莊玩家
  //     setDealerState({
  //       name: userState[setting?.startDealerPosition].name,
  //       winningStreakCount: 0,
  //       position: setting?.startDealerPosition,
  //     })
  //     Toast.show({
  //       content: <div className="">牌局開始</div>,
  //       position: 'top',
  //     })
  //   }
  // }

  const winOptions = useMemo(() => {
    const loserOption = userOption.map((option) => {
      return {
        ...option,
        label: option?.value === currentPosition ? '自摸' : option?.label,
      }
    })
    return [bonusOptions, loserOption]
  }, [userOption, currentPosition])

  const onClickStartNewGame = () => {
    // if (gameRecord?.status !== GameRecordStatus.PLAYING) return
    setEndGameModalOpen(true)
  }

  const onClickNoOneWin = () => {
    setIsNoOneWinModalOpen(true)
  }

  const swiperRef = useRef<SwiperRef>(null)
  const [activeIndex, setActiveIndex] = useState(0)

  const onTabClick = (index: number) => {
    setActiveIndex(index)
    swiperRef.current?.swipeTo(index)
  }

  return (
    <div className="bg-[#FFFCEC]">
      {gameRecord && (
        <>
          <Swiper
            className="recordContentHeight"
            direction="horizontal"
            loop={false}
            indicator={() => null}
            ref={swiperRef}
            defaultIndex={activeIndex}
            onIndexChange={(index) => {
              setActiveIndex(index)
            }}
          >
            <Swiper.Item>
              {/* Header */}
              <div className="h-[50px] flex justify-start items-center w-full text-base">
                {identity === UserIdentity.SPECTATOR ? (
                  <div
                    className={clsx('flex flex-col justify-center items-center w-1/3')}
                    onClick={() => navigate('/')}
                  >
                    <span className="font-bold flex justify-center items-center h-[24px] w-[24px]">
                      <ArrowLeft />
                    </span>
                    <span>返回首頁</span>
                  </div>
                ) : (
                  <div
                    className={clsx('flex flex-col justify-center items-center w-1/3')}
                    onClick={() => navigate('/menu')}
                  >
                    <span className="font-bold flex justify-center items-center h-[24px] w-[24px]">
                      <ArrowLeft />
                    </span>
                    <span>返回主選單</span>
                  </div>
                )}
                {identity === UserIdentity.OWNER ? (
                  <div
                    className={clsx('flex flex-col justify-center items-center w-1/3')}
                    onClick={onClickStartNewGame}
                  >
                    <span className="text-[green] font-bold flex justify-center items-center h-[24px] w-[24px]">
                      <Money />
                    </span>

                    <span className="font-bold">結束牌局</span>
                  </div>
                ) : (
                  <div className="w-1/3"></div>
                )}

                <div className={clsx('flex flex-col justify-center items-center w-1/3')} onClick={copyUrl}>
                  <span className="font-bold flex justify-center items-center h-[24px] w-[24px]">
                    <Share />
                  </span>

                  <span>分享此牌局</span>
                </div>
              </div>

              {/* Content */}
              <div className="contentHeight relative">
                {/* 風和圈 */}
                <div className=" absolute top-[2%] left-[50%] translate-x-[-50%] translate-y-[-2%] flex text-base">
                  <div className="flex justify-center items-center rounded py-1 px-3 bg-[#FFECC9] text-[#B87800]">
                    {`${gameRecord?.wind}圈${gameRecord?.round}風`}
                  </div>
                  {/* <div className="ml-4" onClick={sendWebsocket}>
                    send{' '}
                  </div> */}
                </div>

                {positionArray.map((position: PositionType) => {
                  // return <UserBox position={position} />;
                  return UserBox(position)
                })}

                <div className=" absolute top-[50%] left-[50%] translate-x-[-50%] translate-y-[-50%] ">
                  {gameRecord?.status === GameRecordStatus.PREPARING ? (
                    gameRecord.users.length === 4 ? (
                      identity === UserIdentity.OWNER ? (
                        <div
                          className={clsx(
                            'p-2 rounded text-lg',
                            isReady ? ' bg-blue-600 text-[#fff]' : ' bg-gray-500 text-gray-900'
                          )}
                          onClick={() => {
                            if (!isReady) {
                              Toast.show({
                                content: '請先加入所有玩家',
                                position: 'top',
                                afterClose: () => {},
                              })
                            } else {
                              if (gameRecord?.status === GameRecordStatus.PLAYING) return
                              setIsSettingModalOpen(true)
                            }
                          }}
                        >
                          開始牌局
                        </div>
                      ) : (
                        <div className="p-2 rounded text-base text-[brown] font-bold bg-[white]">等待房主開始</div>
                      )
                    ) : (
                      <div className="p-2 rounded text-base text-[brown] font-bold bg-[white]">等待玩家加入</div>
                    )
                  ) : gameRecord?.status === GameRecordStatus.PLAYING ? (
                    identity === UserIdentity.OWNER ? (
                      <div
                        className="py-1 px-3 bg-[white] rounded overflow-hidden text-base shadow-[0px_4px_12px_rgba(0,0,0,0.1)]"
                        onClick={onClickNoOneWin}
                      >
                        流局
                      </div>
                    ) : identity === UserIdentity.PLAYER ? (
                      <div className="p-2 rounded text-base text-[brown] font-bold bg-[white]">參賽中</div>
                    ) : (
                      <div className="p-2 rounded text-base text-[brown] font-bold bg-[white]">觀賽中</div>
                    )
                  ) : (
                    gameRecord?.status === GameRecordStatus.FINISH && (
                      <div className="p-2 rounded text-base text-[brown] font-bold bg-[white]">牌局已結束</div>
                    )
                  )}
                </div>
              </div>

              {/* 點擊玩家視窗放大時，背後的遮罩 */}
              {currentPosition && (
                <div
                  className={clsx('absolute top-0 left-0 w-[100vw] vh100 z-[5]')}
                  onClick={() => {
                    setCurrentPosition('')
                  }}
                ></div>
              )}

              {/* 牌局設定Modal */}
              <div
                className={clsx(
                  'vh100 absolute top-0 left-0 w-[100vw] bg-black-1/10 z-[5] justify-center items-center',
                  isSettingModalOpen ? 'flex' : 'hidden'
                )}
                onClick={() => setIsSettingModalOpen(false)}
              >
                <div className="z-10 w-[80vw] p-4 bg-[#fff] rounded-3xl" onClick={(e) => e.stopPropagation()}>
                  <div className="mb-10 text-center text-2xl font-bold">牌局設定</div>
                  <div className="mb-10 text-xl">
                    <div className="flex mb-3">
                      <div className="w-1/2 flex justify-center items-center">底</div>
                      <div className="w-1/2 flex justify-center items-center">
                        <Picker
                          columns={baseArray}
                          value={[gameRecord?.setting?.base]}
                          onConfirm={(e) => onSettingChange('base', e[0])}
                          onSelect={(val, extend) => {}}
                        >
                          {(items, { open }) => {
                            return (
                              <Button
                                className="!py-[5px] !px-[20px] !border-0 !bg-blue-400 !text-[#fff]"
                                onClick={open}
                              >
                                {items.every((item) => item === null)
                                  ? '未选择'
                                  : items.map((item) => item?.label ?? '未选择').join(' - ')}
                              </Button>
                            )
                          }}
                        </Picker>
                      </div>
                    </div>
                    <div className="flex mb-3">
                      <div className="w-1/2 flex justify-center items-center">台</div>
                      <div className="w-1/2 flex justify-center items-center">
                        <Picker
                          columns={baseArray}
                          value={[gameRecord?.setting?.bonus]}
                          onConfirm={(e) => onSettingChange('bonus', e[0])}
                          onSelect={(val, extend) => {}}
                        >
                          {(items, { open }) => {
                            return (
                              <Button
                                className="!py-[5px] !px-[20px] !border-0 !bg-blue-400 !text-[#fff]"
                                onClick={open}
                              >
                                {items.every((item) => item === null)
                                  ? '未选择'
                                  : items.map((item) => item?.label ?? '未选择').join(' - ')}
                              </Button>
                            )
                          }}
                        </Picker>
                      </div>
                    </div>
                    <div className="flex mb-3">
                      <div className="w-1/2 text-center">起莊</div>
                      <div className="w-1/2 flex justify-center items-center">
                        <Picker
                          columns={[userOption] || []}
                          value={[gameRecord?.setting?.startDealerPosition]}
                          onConfirm={(e) => onSettingChange('startDealerPosition', e[0])}
                          onSelect={(val, extend) => {}}
                        >
                          {(items, { open }) => {
                            return (
                              <Button
                                className="!py-[5px] !px-[20px] !border-0 !bg-blue-400 !text-[#fff]"
                                onClick={open}
                              >
                                {items.every((item) => item === null)
                                  ? '未选择'
                                  : items.map((item) => item?.label ?? '未选择').join(' - ')}
                              </Button>
                            )
                          }}
                        </Picker>
                      </div>
                    </div>
                  </div>
                  <div className="flex justify-center items-center relative top-0">
                    <div
                      className="flex justify-center items-center bg-blue-600 text-[#fff] py-2 px-12 rounded text-xl"
                      onClick={startGame}
                    >
                      開始
                    </div>
                  </div>
                </div>
              </div>

              {/* 確認是否結束此牌局 */}
              <div
                className={clsx(
                  'vh100 absolute top-0 left-0 w-[100vw] bg-black-5/10 z-[5] justify-center items-center',
                  isEndGameModalOpen ? 'flex' : 'hidden'
                )}
                onClick={() => setEndGameModalOpen(false)}
              >
                <div className="z-10 w-[80vw] p-4 bg-[#fff] rounded-3xl" onClick={(e) => e.stopPropagation()}>
                  <div className="mb-7 text-center text-2xl font-[400] text-[red]">
                    確定要結束此牌局嗎？
                    <br />
                    確定要結束此牌局嗎？
                    <br />
                    確定要結束此牌局嗎？
                  </div>

                  <div className="flex justify-around items-center text-lg">
                    <div className="py-1 text-[#929292]" onClick={() => setEndGameModalOpen(false)}>
                      取消
                    </div>
                    <div
                      className="py-1 font-bold text-[green]"
                      onClick={() => {
                        setEndGameModalOpen(false)
                        endGame()
                      }}
                    >
                      確定
                    </div>
                  </div>
                </div>
              </div>

              {/* 確認是否流局 */}
              <div
                className={clsx(
                  'vh100 absolute top-0 left-0 w-[100vw] bg-black-5/10 z-[5] justify-center items-center',
                  isNoOneWinModalOpen ? 'flex' : 'hidden'
                )}
                onClick={() => setIsNoOneWinModalOpen(false)}
              >
                <div className="z-10 w-[80vw] p-4 bg-[#fff] rounded-3xl" onClick={(e) => e.stopPropagation()}>
                  <div className="mb-7 text-center text-2xl font-[400]">確定流局？</div>

                  <div className="flex justify-around items-center text-lg">
                    <div className="py-1 text-[#929292]" onClick={() => setIsNoOneWinModalOpen(false)}>
                      取消
                    </div>
                    <div
                      className="py-1 font-bold text-[green]"
                      onClick={() => {
                        setIsNoOneWinModalOpen(false)
                        noOneWinTheGame()
                      }}
                    >
                      確定
                    </div>
                  </div>
                </div>
              </div>
            </Swiper.Item>
            <Swiper.Item>
              <Record userState={gameRecord} record={gameRecord?.record || []} />
            </Swiper.Item>
          </Swiper>

          {/* Footer */}
          <div className="text-sm h-[50px] flex justify-around items-center bg-white">
            <div
              className={clsx(
                'flex flex-col justify-center items-center w-1/2',
                activeIndex === 0 ? 'text-[green]' : ''
              )}
              onClick={() => onTabClick(0)}
            >
              <span className="flex justify-center items-center h-[24px] w-[24px]">
                <Battle />
              </span>

              <span>現時賽果</span>
            </div>
            <div
              className={clsx(
                'flex flex-col justify-center items-center w-1/2',
                activeIndex === 1 ? 'text-[green]' : ''
              )}
              onClick={() => onTabClick(1)}
            >
              <span className="flex justify-center items-center h-[24px] w-[24px]">
                <Histogram />
              </span>

              <span>牌局紀錄</span>
            </div>
          </div>
        </>
      )}
    </div>
  )
}
