//@ts-nocheck
import create from 'zustand'
import { persist } from 'zustand/middleware'

const windArray = ['東', '南', '西', '北', '中', '發', '白']

const roundArray = ['東', '南', '西', '北']

const positionArray = ['top', 'right', 'bottom', 'left']

type SettingType = {
  base: number
  bonus: number
  startDealerPosition: string
}

type DealerStateType = {
  name: string
  winningStreakCount: number
  position: string
}

type UserStateType = {
  name: string
  record: number[]
}

type UserStateStoreType = {
  userState: { [key: string]: UserStateType }
  setUserState: (userState: { [key: string]: UserStateType }) => void
  dealerState: DealerStateType
  setDealerState: (dealerState: DealerStateType) => void
}

/**
 * 每局紀錄
 * @param round                    圈
 * @param wind                     風
 * @param timer                    倍率
 * @param bonus                    台數
 * @param winnerPosition            贏的位子
 * @param loserPosition            輸的位子
 *       各位子玩家狀態
 * @param amount                   金額
 * @param isDealer                 是否為莊家
 * @param winningStreakCount       連幾
 */
export type RecordType = {
  round: string
  wind: string
  timer: number
  bonus: number
  winnerPosition: string
  loserPosition: string
  top: {
    amount: number
    isDealer: boolean
    winningStreakCount: number
  }
  right: {
    amount: number
    isDealer: boolean
    winningStreakCount: number
  }
  bottom: {
    amount: number
    isDealer: boolean
    winningStreakCount: number
  }
  left: {
    amount: number
    isDealer: boolean
    winningStreakCount: number
  }
}

/**
 * 當前牌局設定及牌局狀態
 * @param userState                各玩家狀態(名稱、輸贏、賽局紀錄)
 * @param dealerState              目前莊家狀態(莊家是誰，連幾)
 * @param setting                  牌局設定(底、台、起莊是誰)
 * @param wind                     風
 * @param round                    圈
 * @param startTime                開始賽局-時間戳記
 * @param endTime                  結束賽局-時間戳記
 * @param record                   每一把的紀錄
 * @param gameRecord               過往牌局紀錄
 */
type InitType = {
  userState: { [key: string]: UserStateType }
  dealerState: DealerStateType
  setting: SettingType
  wind: string
  round: string
  startTime: number | null
  endTime: number | null
  gameRecord: InitType[]
  record: RecordType[]
}

const init: InitType = {
  userState: {
    top: {
      name: '',
      record: [],
    },
    right: {
      name: '',
      record: [],
    },
    bottom: {
      name: '',
      record: [],
    },
    left: {
      name: '',
      record: [],
    },
  },
  dealerState: {
    name: '',
    winningStreakCount: 0,
    position: '',
  },
  setting: {
    base: 30,
    bonus: 10,
    startDealerPosition: '',
  },
  wind: '東',
  round: '東',
  startTime: null,
  endTime: null,
  record: [],
  gameRecord: [],
}

export const UserStateStore = create(
  persist(
    (set, get: any) => ({
      ...init,
      setUserState: (userState: { [key: string]: UserStateType }) => {
        set({
          userState: userState,
        })
      },
      setDealerState: (dealerState: DealerStateType) => {
        set({
          dealerState: dealerState,
          startTime: new Date().getTime(),
        })
      },
      // 觸發 莊家及風圈 機制輪轉
      win: (winnerPosition: string) => {
        // 連莊
        if (winnerPosition === get().dealerState.position) {
          set({
            dealerState: {
              ...get().dealerState,
              winningStreakCount: get().dealerState.winningStreakCount + 1,
            },
          })
        } else {
          // 莊家輪轉的機制
          const userArray = Object.keys(get().userState).map((key) => {
            return get().userState[key].name
          })

          const currentIndex = userArray.indexOf(get().dealerState.name)
          const nextIndex = currentIndex - 1 < 0 ? 3 : currentIndex - 1
          const nextDealer = userArray[nextIndex]

          const currentPositionIndex = positionArray.indexOf(get().dealerState.position)
          const nextPositionIndex = currentPositionIndex - 1 < 0 ? 3 : currentPositionIndex - 1
          const nextPosition = positionArray[nextPositionIndex]
          set({
            dealerState: {
              name: nextDealer,
              winningStreakCount: 0,
              position: nextPosition,
            },
          })

          // 風跟圈的輪環機制。
          const currentWindIndex = windArray.indexOf(get().wind)
          const currentRoundIndex = roundArray.indexOf(get().round)

          if (currentRoundIndex === 3) {
            set({
              wind: currentWindIndex + 1 > 6 ? windArray[0] : windArray[currentWindIndex + 1],
              round: roundArray[0],
            })
          } else {
            set({
              round: roundArray[currentRoundIndex + 1],
            })
          }
        }
      },
      setSetting: (setting: SettingType) => {
        set({
          setting: setting,
        })
      },
      startNewGame: () => {
        const record = {
          ...get(),
          endTime: new Date().getTime(),
        }

        let newRecord: InitType[] = [].concat(record.gameRecord)
        newRecord.push(record)

        set({
          ...init,
          gameRecord: newRecord,
        })
      },
      // 流局
      noOneWinTheGame: () => {
        const currentDealerState = get().dealerState

        set({
          dealerState: {
            name: currentDealerState.name,
            winningStreakCount: currentDealerState.winningStreakCount + 1,
            position: currentDealerState.position,
          },
        })

        const record = {
          round: get().round,
          wind: get().wind,
          timer: 1,
          bonus: 0,
          winnerPosition: '',
          loserPosition: '',
          top: {
            amount: 0,
          },
          right: {
            amount: 0,
          },
          bottom: {
            amount: 0,
          },
          left: {
            amount: 0,
          },
        }

        positionArray.forEach((position) => {
          record[position].isDealer = get().dealerState.position === position
          record[position].winningStreakCount =
            get().dealerState.position === position ? currentDealerState.winningStreakCount + 1 : 0
        })

        const newRecord = get().record
        newRecord.push(record)

        set({
          record: newRecord,
        })
      },
      addRecord: (record: RecordType) => {
        const formatRecord = { ...record }
        // positionArray.forEach((position) => {
        //   formatRecord[position].isDealer = record.winnerPosition === position
        //   formatRecord[position].winningStreakCount =
        //     record.winnerPosition === position
        //       ? record.winnerPosition === get().dealerState.position
        //         ? get().dealerState.winningStreakCount - 1
        //         : 0
        //       : 0
        // })

        // formatRecord.round = get().round
        // formatRecord.wind = get().wind

        const newRecord = get().record
        newRecord.push(formatRecord)

        set({
          record: newRecord,
        })
      },
    }),
    {
      name: 'userState-storage',
      getStorage: () => localStorage,
    }
  )
)
