// reset CSS
import './style/reset.css'
import { Route, Routes, HashRouter } from 'react-router-dom'
import { HomePage, OfflinePage, Online, Login, Register, Menu } from './pages'
import { useEffect } from 'react'
import { layoutStore, LayoutStoreStateType } from './store/LayoutStore'
import { ReactComponent as Loading } from './assets/ic_loading-spinner.svg'
import { connectWebSocket } from './websocket'

function App() {
  const { isLoading } = layoutStore((state: LayoutStoreStateType) => ({
    isLoading: state.isLoading,
  }))

  // useEffect(() => {
  //   connectWebSocket()
  // }, [])

  return (
    <div className="App vh100 relative">
      <HashRouter>
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/offline" element={<OfflinePage />} />
          <Route path="/online/:gameRecordId" element={<Online />} />
          <Route path="/login" element={<Login />} />
          <Route path="/register" element={<Register />} />
          <Route path="/menu" element={<Menu />} />
        </Routes>
      </HashRouter>
      {isLoading && (
        <div className="absolute w-full vh100 top-0 left-0 bg-black-3/10 z-[5] flex justify-center items-center">
          <span className="loading w-[100px] h-[100px] text-[#fefbf9]">
            <Loading />
          </span>
        </div>
      )}
    </div>
  )
}

export default App

